import {
  Card,
  CardBody,
  CardHeader,
  Badge,
  Button,
  CardTitle,
  Row,
  Col,
  Input,
  Label,
  FormGroup
} from 'reactstrap'
import Radio from '../../../../components/@vuexy/radio/RadioVuexy'
import NumericInput from 'react-numeric-input'
import {
  Star,
  X,
  Heart,
  ShoppingCart,
  Home,
  CreditCard,
  PlusSquare
} from 'react-feather'
import { mobileStyle } from '../../../forms/form-elements/number-input/InputStyles'
import Breacrumbs from '../../../../components/@vuexy/breadCrumbs/BreadCrumb'
import bankLogo from '../../../../assets/img/pages/eCommerce/bank.png'
import Wizard from '../../../../components/@vuexy/wizard/WizardComponent'
import { data } from '../shop/shopData'
import { productsList } from './cartData'
import { AvInput, AvGroup, AvFeedback } from 'availity-reactstrap-validation'

import '../../../../assets/scss/pages/app-ecommerce-shop.scss'
import 'react-toastify/dist/ReactToastify.css'
import '../../../../assets/scss/plugins/extensions/toastr.scss'
import axios from 'axios'
//axios.defaults.baseURL = "http://localhost:5000";
axios.defaults.baseURL = "https://paragonheathcare.herokuapp.com";

class Checkout extends React.Component {
  state = {
    activeStep: 0,
    total: 0,
    total1: data.map(item => item.price).reduce((prev, next) => prev + next, 0),
    steps: [],
    
    steps1: [
      
    ]
  }

  addAddess = () => {
    console.log('stateeeeeee', this.state);
    const {name,house,contact,city,pincode,state,landmark} = this.state;
    const prod = JSON.parse(localStorage.getItem('logInUserData'))
    prod.address.push({name,house,contact,city,pincode,state,landmark});
    localStorage.setItem('logInUserData',JSON.stringify(prod));
    const postBody= {
      _id: prod._id,
      address: prod.address
    }
    axios.post('/paragon/api//update/user',postBody).then((res) => {
     // console.log('ressssss', res.data);
     // this.setState({data: res.data})
    
    
    })
    this.setState({selectedAddress: {name,house,contact,city,pincode,state,landmark}})
    this.handleActiveStep(2);
  }

  fetch =()=> {
    const prod = JSON.parse(localStorage.getItem('logInUserData'))
    const body = prod.cart ? prod.cart.map(z => {
      return z.productId
    }) : []
    console.log('body', body)
    axios.post('paragon/api/products/get', { products: body }).then(resp => {
      const data = resp.data
      const { steps } = this.state
      steps.push({
        title: <ShoppingCart size={22} />,
        content: (
          <div className="list-view product-checkout">
            <div className="checkout-items">
              {data.length > 0 ? '' : 'No items in Cart'}
              {data.map((item, i) => {
                const qtyProd = prod.cart.filter(i => i.productId === item.productId);
                data[i].qty=qtyProd[0].qty;
             
                return (
                  <Card className="ecommerce-card" key={i}>
                    <div className="card-content">
                      <div className="item-img text-center">
                        <img
                          src={item.image ? item.image : 'https://i.pinimg.com/736x/da/1f/9e/da1f9e1050f1cc2cccc390a8c1b5aa40.jpg'}
                          style={{
                            maxWidth:
                              window.screen.width < 500 ? '200px' : '300px',
                            maxHeight:
                              window.screen.width < 500 ? '200px' : '300px'
                          }}
                          alt="Product"
                        />
                      </div>
                      <CardBody>
                        <div className="item-name">
                          <span>{item.name}</span>
                          <p className="item-company">
                            By <span className="company-name">{item.by}</span>
                          </p>
                          <p className="stock-status-in">In Stock</p>
                          <div className="item-quantity">
                            <p className="quantity-title">Quantity</p>
                            <NumericInput
                              min={0}
                              max={10}
                              value={qtyProd[0].qty}
                              mobile
                              style={mobileStyle}
                            />
                          </div>
                          <p className="delivery-date">{item.deliveryBy}</p>
                          <p className="offers">
                            <span style={{ color: 'green' }}>
                              MRP ₹<strike>{item.mrp}</strike>
                            </span>{' '}
                            &nbsp;&nbsp;
                            {Math.round(
                              Number(
                                100 -
                                  (Number(item.price) / Number(item.mrp)) * 100
                              )
                            )}
                            % off
                          </p>
                        </div>
                      </CardBody>
                      <div className="item-options text-center">
                        <div className="item-wrapper">
                          <div className="item-rating">
                            <Badge color="success" className="badge-md mr-25">
                              <span className="align-middle">4</span>{' '}
                              <Star size={15} />
                            </Badge>
                          </div>
                          <div className="item-cost">
                            <h6 className="item-price">₹{item.price}</h6>
                          </div>
                        </div>
                        <div className="wishlist" onClick={()=> {this.removefromcart(item);}}>
                          <X size={15} />
                          <span className="align-middle ml-25">Remove</span>
                        </div>
                        <div className="cart"  onClick={()=> {this.movetowishlist(item);}}>
                          <Heart size={15} />
                          <span className="align-middle ml-25">
                            Move To Wishlist
                          </span>
                        </div>
                      </div>
                    </div>
                  </Card>
                )
              })}
            </div>
            <div className="checkout-options">
              <Card>
                <CardBody>
                  <p className="options-title">Options</p>
                  <div className="coupons">
                    <div className="coupons-title">
                      <p>Coupons</p>
                    </div>
                    <div className="apply-coupon">
                      <p>Apply</p>
                    </div>
                  </div>
                  <hr />
                  <div className="price-details">
                    <p>Price Details</p>
                  </div>
                  <div className="detail">
                    <div className="detail-title">Total MRP</div>
                    <div className="detail-amt">
                      
                      {' '}
                      ₹
                      {Math.round(
                        data
                          .map(item => (item.mrp * Number(item.qty)))
                          .reduce((prev, next) => prev + next, 0)
                      )}
                    </div>
                  </div>
                  <div className="detail">
                    <div className="detail-title">Bag Discount</div>
                    <div className="detail-amt discount-amt">
                      -₹
                      {Math.round(
                        data
                          .map(item => item.mrp * item.qty)
                          .reduce((prev, next) => prev + next, 0) -
                          data
                            .map(item => item.price * item.qty)
                            .reduce((prev, next) => prev + next, 0)
                      )}
                    </div>
                  </div>
                  <div className="detail">
                    <div className="detail-title">Estimated Tax</div>
                    <div className="detail-amt">₹0</div>
                  </div>
                  <div className="detail">
                    <div className="detail-title">EMI Eligibility</div>
                    <div className="detail-amt emi-details">Details</div>
                  </div>
                  <div className="detail">
                    <div className="detail-title">Delivery Charges</div>
                    <div className="detail-amt discount-amt">Free</div>
                  </div>
                  <hr />

                  <div className="detail">
                    <div className="detail-title detail-total">Total</div>
                    <div className="detail-amt total-amt">
                      $
                      {Math.round(
                        data
                          .map(item => item.price * item.qty)
                          .reduce((prev, next) => prev + next, 0)
                      )}{' '}
                    </div>
                  </div>
                  <Button.Ripple
                    type="submit"
                    block
                    color="primary"
                    className="btn-block"
                    onClick={() => this.handleActiveStep(1)}
                  >
                    Place Order
                  </Button.Ripple>
                </CardBody>
              </Card>
            </div>
          </div>
        )
      });

      steps.push({
        title: <Home size={22} />,
        content: (
          <div className="list-view product-checkout">
            <Card>
              <CardHeader className="flex-column align-items-start">
                <CardTitle>Add New Address</CardTitle>
                <p className="text-muted mt-25">
                  Be sure to check "Deliver to this address" when you have
                  finished
                </p>
              </CardHeader>
              <CardBody>
                <Row>
                  <Col md="6" sm="12">
                    <AvGroup>
                      <Label for="name"> Name </Label>
                      <AvInput id="name" onChange={(e)=>{this.setState({name:e.target.value})}} name="name" type="text"  />
                      <AvFeedback>Please enter valid Name</AvFeedback>
                    </AvGroup>
                  </Col>
                  <Col md="6" sm="12">
                    <AvGroup>
                      <Label for="contact-number"> Contact Number </Label>
                      <AvInput
                      onChange={(e)=>{this.setState({contact:e.target.value})}}
                        id="contact-number"
                        name="contact-number"
                        type="number"
                        
                      />
                      <AvFeedback>Please enter valid Contact Number</AvFeedback>
                    </AvGroup>
                  </Col>
                  <Col md="6" sm="12">
                    <AvGroup>
                      <Label for="apt-no"> Address </Label>
                      <AvInput id="apt-no"
                      onChange={(e)=>{this.setState({house:e.target.value})}} name="apt-no" type="text"  />
                      <AvFeedback>
                        Please enter valid House Number
                      </AvFeedback>
                    </AvGroup>
                  </Col>
                  <Col md="6" sm="12">
                    <AvGroup>
                      <Label for="landmark">
                        {' '}
                        Landmark e.g. near Times Square{' '}
                      </Label>
                      <AvInput onChange={(e)=>{this.setState({landmark:e.target.value})}}
                      id="landmark" name="landmark" type="text" />
                    </AvGroup>
                  </Col>
                  <Col md="6" sm="12">
                    <AvGroup>
                      <Label for="town-city"> Town/City</Label>
                      <AvInput
                      onChange={(e)=>{this.setState({city:e.target.value})}}
                        id="town-city"
                        name="town-city"
                        type="text"
                        //required
                      />
                      <AvFeedback>Please enter valid Town/City</AvFeedback>
                    </AvGroup>
                  </Col>
                  <Col md="6" sm="12">
                    <AvGroup>
                      <Label for="pincode"> Pincode</Label>
                      <AvInput
                        id="pincode"
                        name="pincode"
                        onChange={(e)=>{this.setState({pincode:e.target.value})}}
                        type="number"
                        //required
                      />
                      <AvFeedback>Please enter valid Pincode</AvFeedback>
                    </AvGroup>
                  </Col>
                  <Col md="6" sm="12">
                    <AvGroup>
                      <Label for="state"> State</Label>
                      <AvInput onChange={(e)=>{this.setState({state:e.target.value})}} id="state" name="state" 
                      type="text" 
                      //required 
                      />
                      <AvFeedback>Please enter valid State</AvFeedback>
                    </AvGroup>
                  </Col>
                  <Col md="6" sm="12">
                    <FormGroup>
                      <Label for="address-type">Address Type</Label>
                      <Input type="select" name="select" id="address-type">
                        <option>Home</option>
                        <option>Work</option>
                      </Input>
                    </FormGroup>
                  </Col>
                  <Col sm="6" md={{ offset: 6, size: 6 }}>
                    <Button.Ripple
                      type="submit"
                      color="primary"
                      onClick={() => this.addAddess()}
                    >
                      SAVE AND DELIVER HERE
                    </Button.Ripple>
                  </Col>
                </Row>
              </CardBody>
            </Card>
            <div className="customer-card">
            {this.state.selectedAddress || prod && prod.address && prod.address.map(addr => {
              return (<Card>
                <CardHeader>
                  <CardTitle>{addr.name}</CardTitle>
                </CardHeader>
                <CardBody>
                <p className="mb-0">Contact : +91-{addr.contact}</p>
                  <p className="mb-0">{addr.house}</p>
                  <p>{addr.landmark}</p>
                  <p>{addr.city} </p>
                  <p>{addr.state} - {addr.pincode}</p>
                  <hr />
                  <Button.Ripple
                    type="submit"
                    color="primary"
                    className="btn-block"
                    onClick={() => this.handleActiveStep(2)}
                  >
                    DELIVER TO THIS ADDRESS
                  </Button.Ripple>
                </CardBody>
              </Card>)
            })
              
          }
          {/*this.state.selectedAddress && 
            <Card>
                <CardHeader>
                  <CardTitle>{this.state.selectedAddress.name}</CardTitle>
                </CardHeader>
                <CardBody>
                <p className="mb-0">Contact : +91-{this.state.selectedAddress.contact}</p>
                  <p className="mb-0">{this.state.selectedAddress.house}</p>
                  <p>{this.state.selectedAddress.landmark}</p>
                  <p>{this.state.selectedAddress.city} </p>
                  <p>{this.state.selectedAddress.state} - {this.state.selectedAddress.pincode}</p>
                  <hr />
                  <Button.Ripple
                    type="submit"
                    color="primary"
                    className="btn-block"
                    onClick={() => this.handleActiveStep(2)}
                  >
                    DELIVER TO THIS ADDRESS
                  </Button.Ripple>
                </CardBody>
              </Card>
              */}
            </div>
          </div>
        )
      })

      steps.push({
        title: <CreditCard size={22} />,
        content: (
          <div className="list-view product-checkout">
            <div className="payment-type">
              <Card>
                <CardHeader className="flex-column align-items-start">
                  <CardTitle>Payment options</CardTitle>
                  <p className="text-muted mt-25">
                    Be sure to click on correct payment option
                  </p>
                </CardHeader>
                <CardBody>
                  <div className="d-flex justify-content-between flex-wrap">
                    <div className="vx-radio-con vx-radio-primary">
                      <input type="radio" name="bank" />
                      <span className="vx-radio">
                        <span className="vx-radio--border"></span>
                        <span className="vx-radio--circle"></span>
                      </span>
                      <img src='data:image/png;base64,' 
                      alt="img-placeholder" height="20px" width="20px" />
                      &nbsp;
                      <span>SBI Debit Card 12XX XXXX XXXX 0000</span>
                    </div>
                    <div className="card-holder-name mt-75">Aaquib Faridi</div>
                    <div className="card-expiration-date mt-75">11/2020</div>
                  </div>
                  <div className="customer-cvv mt-1">
                    <div className="form-inline">
                      <Label for="cvv">Enter CVV:</Label>
                      <AvInput
                        type="number"
                        className="input-cvv ml-75 mb-50"
                        id="cvv"
                        name="cvv"
                        required
                      />
                      <Button color="primary" className="ml-50 mb-50">
                        {' '}
                        Continue{' '}
                      </Button>
                    </div>
                  </div>
                  <hr className="my-2" />
                  <ul className="other-payment-options list-unstyled">
                    <li className="py-25">
                      <Radio
                        label="Credit / Debit / ATM Card"
                        color="primary"
                        defaultChecked={false}
                        name="paymentType"
                      />
                    </li>
                    <li className="py-25">
                      <Radio
                        label="Net Banking"
                        color="primary"
                        defaultChecked={false}
                        name="paymentType"
                      />
                    </li>
                    <li className="py-25">
                      <Radio
                        label="EMI (Easy Installment)"
                        color="primary"
                        defaultChecked={false}
                        name="paymentType"
                      />
                    </li>
                    <li className="py-25">
                      <Radio
                        label="Cash On Delivery"
                        color="primary"
                        defaultChecked={false}
                        name="paymentType"
                      />
                    </li>
                  </ul>
                  <hr />
                  <div className="gift-card">
                    <p>
                      <PlusSquare size={22} />
                      <span className="align-middle ml-25">Add Gift Card</span>
                    </p>
                  </div>
                </CardBody>
              </Card>
            </div>
            <div className="amount-payable checkout-options">
              <Card>
                <CardHeader>
                  <CardTitle>Price Details</CardTitle>
                </CardHeader>
                <CardBody>
                  <div className="detail">
                    <div className="details title">Price of 3 items</div>
                    <div className="detail-amt">
                      <strong>$699.30</strong>
                    </div>
                  </div>
                  <div className="detail">
                    <div className="details title">Delivery Charges</div>
                    <div className="detail-amt discount-amt">
                      <strong>Free</strong>
                    </div>
                  </div>
                  <hr />
                  <div className="detail">
                    <div className="details title">Amount Payable</div>
                    <div className="detail-amt">
                      <strong>$699.30</strong>
                    </div>
                  </div>
                </CardBody>
              </Card>
            </div>
          </div>
        )
      })

      this.setState({
        steps,
        total: data
          .map(item => item.price)
          .reduce((prev, next) => prev + next, 0)
        
      });
    })
  }
  componentWillMount = () => {
   this.fetch();
  }
  handleActiveStep = index => {
    this.setState({ activeStep: index })
  }

  onValidationError = errors => {
    toast.error('Please Enter Valid Details', {
      position: toast.POSITION.BOTTOM_RIGHT
    })
  }

  movetowishlist = (item) => {
    let user =  JSON.parse(localStorage.getItem('logInUserData'))
    const idx =user.cart.findIndex(i => i.productId === item.productId);
    user.wishlist.push(user.cart[idx].productId);
    if(idx >= 0) {
      user.cart.splice(idx, 1);
    }
    const postBody= {
      _id: user._id,
      cart: user.cart,
      wishlist: user.wishlist
    }
    axios.post('/paragon/api//update/user',postBody).then((res) => {
     // console.log('ressssss', res.data);
     // this.setState({data: res.data})
    
    
    })
    
    localStorage.setItem('logInUserData',JSON.stringify(user));
    window.location.reload();

  }
  removefromcart = (item) => {
    let user =  JSON.parse(localStorage.getItem('logInUserData'))
    const idx =user.cart.findIndex(i => i.productId === item.productId);
    if(idx >= 0) {
      user.cart.splice(idx, 1);
    }
    const postBody= {
      _id: user._id,
      cart: user.cart
    }
    axios.post('/paragon/api//update/user',postBody).then((res) => {
     // console.log('ressssss', res.data);
     // this.setState({data: res.data})
    
    
    })
    
    localStorage.setItem('logInUserData',JSON.stringify(user));
    window.location.reload();

  }

  render() {
    const { steps, activeStep } = this.state
    return (
      <React.Fragment>
        <Breacrumbs
          breadCrumbTitle="Checkout"
          breadCrumbParent="eCommerce"
          breadCrumbActive="Checkout"
        />
        <div className="ecommerce-application">
          <Wizard
            steps={steps}
            activeStep={activeStep}
            pagination={false}
            enableAllSteps
            validate
            tabPaneClass="mt-5"
            onValidationError={this.onValidationError}
          />
          <ToastContainer />
          {console.log('total', this.state.total)}
        </div>
      </React.Fragment>
    )
  }
}

export default Checkout
